	module.exports = {"data":{"markdownRemark":{"html":"<p>CCTVの国際女子バスケットボール選手権大会の現実は非常に激しく、スコアは非常に厳しいものでした。最後に、中国の女子バスケットボールチームがオーストラリアを1ポイント上回りました。現時点では、説明できない中国の複雑な心理的アイデンティティを深く感じています。実際、ほとんどの中国人民の血では、消えることのできないナショナリズムがあります。この種の熾烈な競争は、ほとんどの人にとってあまりにも遠すぎるものの、現時点では、中国のすべての人々は、中国チームが勝つことを願っています。外国人チームが2つだけであれば、レベルは高くなり、中国人はあまり感情的なインプットを持たないでしょう。この種の状況は学校で最も鮮明です。大規模な競技がある場合は、学生が自分の気持ちをコントロールできないことが最も恐ろしいことです。私たちがシンガポールで議論したとき、中国の人々はいつも私たちを支え、勝つことを望んでいました。私たちが勝利したとき、人々は涙を流しました。これは人々の力の巨大な源です。</p>\n<p>心理学の力は、常に社会にとっての基本的な原動力であり、時には最も強力な力源でもあります。この種の電源は、起動後は強力ですが、破壊的な力でもあります。動員がなければ、静かでなく、目に見えない力はありません。近代化の過程では、このような心理的な動機付けに注意を払う必要があると思われます。私たちはそれを立ち上げなければなりませんが、正しいポイントに立ち上げる必要があります。物質的な力はもっと話され、心理的な力はあまり話されていません。 1億人の人口を抱える大きな国で、心理的パワーはどれほど大きいのですか。それが社会の目的でない場合、要素によって動員された後、それは災害になります。それが社会の意図的な打ち出しであれば、それは毛沢東の「霊的な原爆」です。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0110.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0110.md","frontmatter":{"title":"6月10日 金曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0110.md absPath of file >>> MarkdownRemark"}}

